import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

let routes = [];

/**
 * Please don' t remove this.
 * Artefact will use it to inject code
 */
//<= inject_routes =>
routes = routes.concat([
  { path: "/", component: require("../artefact/At/main").default },
  {
    name: "login",
    path: "/login",
    component: require("../artefact/At/Pages/Login").default,
    redirect: "",
  },
  {
    name: "signup",
    path: "/signup",
    component: require("../artefact/At/Pages/Signup").default,
    redirect: "",
  },
  {
    name: "profile",
    path: "/profile",
    component: require("../artefact/At/Pages/Profile").default,
    redirect: "",
  },
  {
    name: "learning",
    path: "/learning",
    component: require("../artefact/At/Pages/Learning").default,
    redirect: "",
  },
  {
    name: "learningDetails",
    path: "/learning/:slug",
    component: require("../artefact/At/Pages/LearningDetails").default,
    redirect: "",
  },
  {
    name: "learningMoodleDetails",
    path: "/learning/m/:id",
    component: require("../artefact/At/Pages/LearningMoodleDetails").default,
    redirect: "",
  },
]);
//<= end_routes =>

const router = new VueRouter({
  mode: "history",
  routes,
});

export default router;
